import { mapState } from 'vuex'
import { upyunUrl } from '../../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      obj: ''
    }
  },
  onLoad (options) {
    this.obj = JSON.parse(decodeURIComponent(options.oJson))
    if (this.obj.type) {
      uni.setNavigationBarTitle({
        title: '收入详情'
      })
    } else {
      uni.setNavigationBarTitle({
        title: '支出详情'
      })
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
  }
}
